// @ts-nocheck

import "./App.css";
import React, {
  useEffect,
  useState,
  useContext
} from "react";

//ui
import { Button, TextField } from "@material-ui/core";

//logic
import io from "socket.io-client";
import { DAppClient } from "@airgap/beacon-sdk";
//import _ from "underscore";
import { v4 as uuidv4 } from 'uuid';
import { FirebaseContext } from "./firebaseContext";
import { useSnackbar } from "notistack";
import 'emoji-mart/css/emoji-mart.css'
import { Picker } from 'emoji-mart'

const socketURL =
  window.location.hostname === "localhost"
    ? "ws://localhost:8000"
    : "wss://network2-backend.herokuapp.com";


const socket = io(socketURL, { transports: ["websocket"] });
const dAppClient = new DAppClient({ name: "Beacon Docs" });

const tempID = uuidv4();
function App() {

  const [activeAccount, setActiveAccount] = useState();
  const [synced, setSynced] = useState('sync');
  const [showUnsync, setShowUnsync] = useState(false);
  const [emoji, setEmoji] = useState();
  const { getProfileFB, setProfileFB, getAllProfilesFB } = useContext(FirebaseContext);
  const [profile, setProfile] = useState({ emoji: emoji, timestamp: Date.now(), key: tempID, username: "Anon" });
  const [profiles, setProfiles] = useState(
    [
      { emoji: "blue", timestamp: Date.now(), key: tempID, username: "user x" },
    ]);
  const [usernameInput, setUsernameInput] = React.useState('Anon');
  const { enqueueSnackbar } = useSnackbar();
  const [statusHistory, setStatusHistory] = useState(
    [
      { emoji: "blue", timestamp: Date.now(), key: tempID, username: "user x" },
    ]);


  const handleChangeUsername = (event: React.ChangeEvent<HTMLInputElement>) => {
    setUsernameInput(event.target.value);
  };

  useEffect(() => {

    async function getProfiles() {
      let result = await getAllProfilesFB();
      setProfiles(result.recentStatus)
      setStatusHistory(result.history)
    }
    getProfiles();

  }, []);


  useEffect(() => {

    const onProfileChange = (data) => {
      setStatusHistory([data].concat(statusHistory));

      //recent
      profiles.find(function (prof, index) {
        if (prof.key === data.key) {
          setProfiles([
            ...profiles.slice(0, index),
            data,
            ...profiles.slice(index + 1)
          ]
          )
          return true;
        }
        return false;
      });


    };


    socket.on('profile', onProfileChange);

    return () => {
      socket.off('profile', onProfileChange);


    };
  }, [profiles, setProfiles]);


  //fetch wallet name if it exist for example, trydrum.tez
  async function getDomain(address: string) {
    let domain;
    await fetch('https://api.tezos.domains/graphql', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        query: `
            {
              reverseRecord(address: "`+ address + `"){owner domain{name}}
            }
            `,
        variables: {
        },
      }
      ),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.data.reverseRecord) {
          domain = result.data.reverseRecord.domain.name;
          setSynced(domain);
          setProfile({ ...profile, username: domain })
        }
      });
  }

  useEffect(() => {
    async function getAcc() {
      setActiveAccount(await dAppClient.getActiveAccount());
      if (activeAccount) {
        setSynced(activeAccount.address.slice(0, 6) + "..." + activeAccount.address.slice(32, 36));
        setShowUnsync(true);
        getDomain(activeAccount.address);
        let tempProfile = await getProfileFB(activeAccount.address)
        setProfile(tempProfile);
        setUsernameInput(tempProfile.username)
      }
      else {
        setSynced('sync');
        setShowUnsync(false);
      }
    }
    getAcc();

  }, [activeAccount]);



  async function unsync() {
    setActiveAccount(await dAppClient.getActiveAccount())
    if (activeAccount) {
      // User already has account connected, everything is ready
      dAppClient.clearActiveAccount().then(async () => {
        setActiveAccount(await dAppClient.getActiveAccount())
        setSynced('sync');
        setShowUnsync(false);
      });
    }
  }

  async function sync() {
    setActiveAccount(await dAppClient.getActiveAccount())
    //Already connected
    if (activeAccount) {
      setSynced(activeAccount.address)
      setShowUnsync(true);
      getDomain(activeAccount.address);

      return activeAccount;
    }
    // The user is not synced yet
    else {
      try {
        console.log("Requesting permissions...");
        const permissions = await dAppClient.requestPermissions();
        setActiveAccount(await dAppClient.getActiveAccount())
        console.log("Got permissions:", permissions.address);
        setSynced(permissions.address)
        setShowUnsync(true);

        getDomain(permissions.address);

      }
      catch (error) {
        console.log("Got error:", error);
      }
    }
  }



  return (
    <div >
      <div className="top-left" style={{ fontSize: "1em", display: "flex", alignItems: "center", margin: 10 }} >
        <b>Network1</b>
      </div>

      <div style={{ fontSize: "0.9em", marginTop: 13, marginLeft: 13 }}>
        <b>History</b>
      </div>
      <div style={{ display: "flex", width: "90%", marginLeft: "auto", marginRight: "auto", overflowX: "scroll" }} >



        {statusHistory && statusHistory.map((profile) => (
          <div key={profile.key} style={{ textAlign: "center", margin: 6 }}>
            <div style={{ width: 60, height: 30, border: "solid 4px ", margin: 4 }}>
              <div style={{ textAlign: "center", fontSize: "1.4em" }}>
                {profile.emoji}
              </div>
            </div>

            <Button title={profile.key} size={"small"} onClick={async () => {
              navigator.clipboard.writeText(profile.key);
              enqueueSnackbar("Address copied ! " + profile.key, {
                variant: "success",
              });
            }} >{profile.username} </Button>
          </div>
        ))
        }

      </div>

      <div style={{ fontSize: "0.9em", marginTop: 6, marginLeft: 13 }}>
        <b>Recent Status</b>
      </div>
      <div style={{ display: "flex", width: "90%", marginLeft: "auto", marginRight: "auto", overflowX: "scroll" }} >
        <div style={{ textAlign: "center", margin: 6 }}>
          <div style={{ width: 60, height: 30, border: "solid 4px ", margin: 4 }}>
            <div style={{ textAlign: "center", fontSize: "1.4em" }}>
              {emoji}
            </div>
          </div>

          <Button title={profile.key} size={"small"} onClick={async () => {
            navigator.clipboard.writeText(profile.key);
            enqueueSnackbar("Address copied ! " + profile.key, {
              variant: "success",
            });
          }} >{profile.username} </Button>
        </div>

        {profiles && profiles.map((profile) => (
          (!activeAccount || profile.key !== activeAccount.address) &&
          <div key={profile.key} style={{ textAlign: "center", margin: 6 }}>
            <div style={{ width: 60, height: 30, border: "solid 4px ", margin: 4 }}>
              <div style={{ textAlign: "center", fontSize: "1.4em" }}>
                {profile.emoji}
              </div>
            </div>
            <Button title={profile.key} size={"small"} onClick={async () => {
              navigator.clipboard.writeText(profile.key);
              enqueueSnackbar("Address copied ! " + profile.key, {
                variant: "success",
              });
            }} >{profile.username} </Button>
          </div>
        ))
        }

      </div>

      <div style={{ width: "90%", marginLeft: "auto", marginRight: "auto" }} >


        <Picker title='Pick your emoji' emoji='point_up' onSelect={(emoji) => { console.log(emoji); setEmoji(emoji.native) }} />

        <br></br>
        <br></br>
        <div style={{ display: "flex" }}>
          <TextField id="outlined-basic" label="Username" variant="outlined" placeholder="Anon" onChange={handleChangeUsername} value={usernameInput} />
          <Button size={"small"} title={"update status"} onClick={() => {
            if (activeAccount) {
              setProfile({ ...profile, emoji: emoji, key: activeAccount.address, username: usernameInput, timestamp: Date.now() })
              //add socket 
              socket.emit('profile', { ...profile, emoji: emoji, key: activeAccount.address, username: usernameInput, timestamp: Date.now() });
              setProfileFB({ ...profile, emoji: emoji, key: activeAccount.address, username: usernameInput, timestamp: Date.now() });
              setStatusHistory([{ ...profile, emoji: emoji, key: activeAccount.address, username: usernameInput, timestamp: Date.now() }].concat(statusHistory));
              enqueueSnackbar("Status Updated ! ", {
                variant: "success",
              });

            }
            else {
              sync();
            }

          }} >  {activeAccount ? <u>update status</u> : <u>sync to join network1</u>} </Button>
        </div>
      </div>

      <div className="bottom-left" style={{ position: "absolute" }}>
        <Button title={"Adventure Networks"} size={"small"} onClick={() => { }} >  <div style={{ textAlign: "left" }}> Adventure <br></br>Networks </div> </Button>
      </div>

      <div className="bottom-right" style={{ position: "absolute", display: "flex", alignItems: "center" }} >
        {showUnsync && <Button size={"small"} title={"unsync"} onClick={() => { unsync() }} ><u>unsync</u> </Button>}

        {showUnsync && <div> | </div>}
        <Button title={"sync"} size={"small"} onClick={async () => { await sync(); }} ><u>{synced}</u> </Button>

      </div>
    </div >
  );
}

export default App;
